import {axios} from "axios";
export const GET_ALL_GAMES = "GET_ALL_GAMES";
export const GET_GAME_BY_ID ="GET_GAME_BY_ID";
export const GET_GAMES_BY_NAME = "GET_GAMES_BY_NAME";
export const GET_ALL_GENRES = "GET_ALL_GENRES";
export const ADD_GAME = "ADD_GAME";


export const getAllGames = ()=>{
    return async (dispatch)=>{
       let json = axios.get ('http://127.0.0.1:3001/game')
        
       return dispatch({
            type: GET_ALL_GAMES,
            payload: json.data
        })
    
}
}
 export const getGameById =(id)=>{
    return async(dispatch)=>{
        let json = await axios.get(`http://127.0.0.1:3001/game/${id}`)
        return dispatch({
            type: GET_GAME_BY_ID,
            payload: json.data
        })
    }

 }
 export const getGamesByName = (name)=>{
    return async (dispatch)=>{
        let json = await axios.get(`http://127.0.0.1:3001/game/name?search=${name}`)
        return dispatch({
            type: GET_GAMES_BY_NAME,
            payload: json.data
        })
    }
 }
 
 export const getAllGenres = ()=>{
    return async (dispatch)=>{
        let json = await axios.get(`http://127.0.0.1:3001/genre`)
        return dispatch({
            type: GET_ALL_GENRES,
            payload: json.data
        })
    }
 }

 export const addGame =(payload)=>{
    return async(dispatch)=>{
        let json= await axios.post(`http://127.0.0.1:3001/game`, payload)
        return json.data;
    }
 }