import { 
  GET_ALL_GAMES, 
  GET_GAME_BY_ID, 
  GET_GAMES_BY_NAME, 
  // GET_ALL_GENRES, 
  ADD_GAME } from "../action";

const initialState={
  games: [],
  detail: {}
}
const rootReducer = (state = initialState, action)=>{
  switch (action.type) {
    case GET_ALL_GAMES:
      return{
        ...state,
        games: action.payload

      }
      
      case GET_GAME_BY_ID:
        return{
          ...state,
          detail: action.payload
        }

      case GET_GAMES_BY_NAME:
        return{
          ...state,
          games: action.payload
        }  

      case ADD_GAME:
        return{
          ...state
        }
    default:
      return state;
  }
}

export default rootReducer;